@font-face {
  font-family: "Inter";
  src: url("../../vendor/fonts/Inter/Inter-Regular.woff2") format("woff2"),
    url("../../vendor/fonts/Inter/Inter-Regular.woff") format("woff");
}

.new-slaylist {
  background-color: #fff;
  max-width: 800px;
  width: 100%;
  border-radius: 20px;
  padding: 34px 36px;
  position: relative;
  margin: 0 auto;
  margin-top: 10px;
  /* border-bottom: 200px; */
}

.new-slaylist__page {
  padding-bottom: 80px;
}

.new-slaylist__submit-button {
  background-color: rgba(0, 0, 0, 0.3);
  color: #fff;
  border-color: none;
  padding: 8px 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-top: 32px;
  /* font-family: "Cabinet Grotesk", sans-serif; */
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 700;
  background-color: #ac124a;
}

.new-slaylist__button-container {
  display: flex;
  flex-direction: row;
  align-items: first baseline;
  margin: 0 auto;
}

.new-slaylist__add-new-button {
  background-color: rgba(0, 0, 0, 0.3);
  color: #ac124a;
  border-color: none;
  padding: 8px 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  /* margin-top: 32px; */
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 700;
  background-color: pink;
  border: solid #ac124a 1px;
}

.new-slaylist__form {
  padding-top: 25px;
  /* position: sticky; */
}

