.new-slaylist__submit-button:hover {
  /* background-color: rgba(0, 0, 0, 0.6); */
  background-color: #c01e58;
}

.new-slaylist__submit-button:active {
  /* background-color: rgba(0, 0, 0, 1); */
  background-color: #881347;
}

.new-slaylist__input {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  outline: none;
  appearance: none;
  border-style: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  margin-top: 10px;
  margin-bottom: 25px;
  width: 100%;
  font-family: "Cabinet Grotesk", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.new-slaylist__input::placeholder {
  color: rgba(0, 0, 0, 0.4);
  font-family: "Cabinet Grotesk", sans-serif;
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
}

.new-slaylist__input_radio + label {
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
  border: rgba(0, 0, 0, 0.4);
  margin-left: 7px;
}
.new-slaylist__input_radio:checked + label {
  color: rgba(0, 0, 0, 1);
  margin-left: 7px;
}

.new-slaylist__slaylets-container {
  margin-top: 18px;
  margin-bottom: 50px;
}

@media screen and (max-width: 767px) {
  .new-slaylist {
    max-width: calc(100% - 150px);
  }
}

@media screen and (max-width: 479px) {
  .new-slaylist {
    max-width: calc(100% - 120px);
  }
}
